export default {
	props: {
		list: {
			type: Array,
			default(){
				return [];
			}
		},
		params: {
			type: Object,
			default(){
				return {}
			}
		},
		deliverGoods:{
			type: Boolean
		}
	},
	data() {
		return {
			treeList: [],
			treeParams: {
				defaultIcon: '/static/mix-tree/defaultIcon.png',
				currentIcon: '/static/mix-tree/currentIcon.png',
				lastIcon: '',
				border: false
			}
		}
	},
	watch: {
		list(list){
			
			this.treeParams = Object.assign(this.treeParams, this.params);
			
			this.treeList=[]
			this.renderTreeList(list);
		}
	},
	methods: {
		//扁平化树结构
		renderTreeList(list=[], rank=0, parentId=[]){
			
			list.forEach(item=>{
				this.treeList.push({
					id: item.id,
					name: item.name,
					parentId,  // 父级id数组
					rank,  // 层级
					showChild: false,  //子级是否显示
					show: rank === 0  // 自身是否显示
				})
				if(Array.isArray(item.children) && item.children.length > 0){
					let parents = [...parentId];
					parents.push(item.id);
					this.renderTreeList(item.children, rank+1, parents);
				}else{
					this.treeList[this.treeList.length-1].lastRank = true;
				}
				
			})
		},
		deleteCode(item){
			this.$emit('deleteCode', item);
		},
		// 点击
		treeItemTap(item){
			
			let list = this.treeList;
			let id = item.id;
			if(item.lastRank === true){
				//点击最后一级时触发事件
				this.$emit('treeItemClick', item);
				return;
			}
			item.showChild = !item.showChild;
			list.forEach(childItem=>{
				if(item.showChild === false){
					//隐藏所有子级
					if(!childItem.parentId.includes(id)){
						return;
					}
					if(childItem.lastRank !== true){
						childItem.showChild = false;
					}
					childItem.show = false;
				}else{
					if(childItem.parentId[childItem.parentId.length-1] === id){
						childItem.show = true;
					}
				}
			})
		}
	}
}
